.container {
  padding: 50px 30px;
  text-align: center;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  gap: 30px;
  background-color: rgb(238, 252, 255);
  min-height: 85vh;
}
/* .Container_Main{
  padding-top: 10%;
} */

.heading {
  margin: 0 auto;
  max-width: 500px;
  color: #000;
  font-size: 3.1rem;
  line-height: 4.1rem;
  font-weight: bold;
  letter-spacing: 1px;
}

.heading span {
  background-clip: text;
  background-image: linear-gradient(to right, #239ce2, #1369b9);
  color: transparent;
}

.right img {
  width: 400px;
}


@media screen and (max-width: 400px) {
  .heading {
    margin: 0 auto;
    max-width: 100px;
    color: #000;
    font-size: 2.1rem;
    line-height: 3.1rem;
    font-weight: bold;
    letter-spacing: 1px;
  }
  .container {
  margin-top: 15%;
  }
  .heading span {
    background-clip: text;
    background-image: linear-gradient(to right, #239ce2, #1369b9);
    color: transparent;
  }
  
  .right img {
    width: 150px;
    margin-right: 40px;
  }
}